"use client"

import { useEffect, useState } from 'react'
import { SunIcon, MoonIcon } from "@heroicons/react/24/solid";


export default function DarkMode() {
  const [theme, setTheme] = useState<'light'|'dark'>('dark')
  useEffect(()=>{
    const stored = localStorage.getItem('theme')
    if (stored) setTheme(stored as 'light'|'dark')
    document.documentElement.classList.toggle('dark', theme==='dark')
  },[theme])
  return (
    <button aria-label="Toggle theme" onClick={()=>{ const next = theme==='dark'?'light':'dark'; setTheme(next); localStorage.setItem('theme', next) }} className="p-2 bg-white/3 rounded">
      {theme==='dark' ? <SunIcon className="w-5 h-5 text-yellow-300"/> : <MoonIcon className="w-5 h-5 text-indigo-300"/>}
    </button>
  )
}
